:root {
  --strong-cyan: hsl(172, 67%, 45%);
  --very-dark-cyan: hsl(183, 100%, 15%);
  --dark-grayish-cyan: hsl(186, 14%, 43%);
  --darker-grayish-cyan: hsl(184, 14%, 56%);
  --light-grayish-cyan: hsl(185, 41%, 84%);
  --lighter-grayish-cyan: hsl(189, 41%, 97%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --space-mono: "Space Mono", monospace;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--space-mono);
}

label {
  margin: 0;
  padding: 0;
}

label,
h5 {
  font-size: 0.875rem;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

input[type=number] {
  margin: 0;
  padding: 0.4375rem 1.125rem 0.3125rem 1.75rem;
  border: none;
  border-radius: 0.5rem;
  color: var(--very-dark-cyan);
  font-size: 1.5rem;
  text-align: end;
  font-family: inherit;
  font-weight: inherit;
}
input[type=number]::placeholder {
  color: var(--darker-grayish-cyan);
}

input[type=number]::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

button {
  font-family: inherit;
  font-weight: inherit;
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5 {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

button {
  border: none;
  background: none;
}

label {
  margin-top: 0;
  margin-bottom: 0;
}

body {
  background: var(--light-grayish-cyan);
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 3.125rem;
}
@media (min-width: 64em) {
  .container {
    margin-top: 8.75rem;
  }
}

.calculator {
  background: var(--white);
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 2rem 0;
  border-radius: 1.5625rem 1.5625rem 0 0;
}
@media (min-width: 40em) {
  .calculator {
    max-width: 57.5rem;
    flex-direction: row;
    border-radius: 1.5625rem;
  }
}
@media (min-width: 64em) {
  .calculator {
    max-width: 57.5rem;
    flex-direction: row;
    border-radius: 1.5625rem;
  }
}
.calculator__form {
  max-width: 23.4375rem;
  padding: 0 2rem;
}
@media (min-width: 40em) {
  .calculator__form {
    width: 50%;
    padding: 0 3rem;
    padding-right: 1.25rem;
  }
}
@media (min-width: 64em) {
  .calculator__form {
    flex: 1 0 27.6875rem;
    min-width: 27.6875rem;
    padding: 0 3rem;
    padding-right: 1.25rem;
  }
}
.calculator__results {
  padding: 0 0.5rem;
}
@media (min-width: 40em) {
  .calculator__results {
    width: 50%;
  }
}
@media (min-width: 64em) {
  .calculator__results {
    min-width: 25.8125rem;
  }
}

.title {
  color: var(--very-dark-cyan);
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 0.6875rem;
  letter-spacing: 0.625rem;
  margin-bottom: 2.5625rem;
}
@media (min-width: 64em) {
  .title {
    margin-bottom: 4.6875rem;
  }
}

.form {
  display: flex;
  flex-direction: column;
  color: var(--dark-grayish-cyan);
  font-weight: 700 !important;
}
.form__bill, .form__tip {
  margin-bottom: 2.4375rem;
}
@media (min-width: 64em) {
  .form__bill-title, .form__tip-title, .form__people-title {
    font-size: 1.125rem;
  }
}
.form__bill {
  display: flex;
  flex-direction: column;
}
.form__bill-title {
  margin-bottom: 0.5rem;
}
.form__bill-input {
  background: no-repeat scroll 1rem 1rem url(../images/icon-dollar.svg), var(--lighter-grayish-cyan);
}
.form__bill-input:focus {
  outline-color: var(--strong-cyan) !important;
}
.form__tip {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1.375rem repeat(3, 1fr);
  gap: 1rem;
  align-items: center;
}
@media (min-width: 64em) {
  .form__tip {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1.375rem repeat(2, 1fr);
    column-gap: 0.875rem;
    margin-bottom: 1.875rem;
  }
}
.form__tip-title {
  grid-column: 1/3;
}
@media (min-width: 64em) {
  .form__tip-title {
    grid-column: 1/4;
  }
}
.form__tip-value {
  background: var(--very-dark-cyan);
  width: 9.125rem;
  height: 3rem;
  border-radius: 0.5rem;
  color: var(--lighter-grayish-cyan);
  font-size: 1.5rem;
}
@media (min-width: 64em) {
  .form__tip-value {
    width: 7.3125rem;
    height: 3rem;
    font-size: 1.5rem;
  }
}
.form__tip-value.active {
  background-color: var(--strong-cyan);
  color: var(--very-dark-cyan);
}
.form__tip-value#tip-value-custom {
  background: var(--lighter-grayish-cyan);
}
.form__tip-value#tip-value-custom::placeholder {
  color: var(--dark-grayish-cyan);
}
.form__tip-value#tip-value-custom:focus {
  outline-color: var(--strong-cyan) !important;
}
@media (min-width: 64em) {
  .form__tip-value#tip-value-custom {
    padding: 0;
    padding-right: 1.125rem;
    font-size: 1.5rem;
  }
}
.form__people {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 2rem;
}
.form__people-title {
  grid-column: 1/2;
  margin-bottom: 0.5rem;
}
.form__people-zero {
  grid-column: 2/3;
  text-align: end;
  color: darkorange;
  display: none;
}
.form__people-input {
  grid-column: 1/3;
  background: no-repeat scroll 1rem 1rem url(../images/icon-person.svg), var(--lighter-grayish-cyan);
}
.form__people-input:focus {
  outline-color: darkorange;
}

.results {
  background: var(--very-dark-cyan);
  border-radius: 0.9375rem;
  color: var(--lighter-grayish-cyan);
  margin: 0 1.4375rem;
  padding: 0 1.4375rem 1.4375rem;
}
@media (min-width: 64em) {
  .results {
    min-width: 25.8125rem;
    flex: 1 0 25.8125rem;
    margin: 0 0;
    margin-right: 2rem;
    margin-left: 1.5625rem;
  }
}
.results__tip {
  margin-top: 2.875rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
}
.results__tip-title {
  grid-column: 1/2;
  font-size: 1.125rem;
}
.results__tip-subtitle {
  grid-column: 1/2;
  font-size: 0.9375rem;
  color: var(--darker-grayish-cyan);
}
.results__tip-total {
  grid-column: 2/3;
  grid-row: 1/3;
  text-align: end;
  font-size: 2.125rem;
  color: var(--strong-cyan);
}
.results__total {
  margin-top: 1.25rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
}
.results__total-title {
  grid-column: 1/2;
  font-size: 1.125rem;
}
.results__total-subtitle {
  grid-column: 1/2;
  font-size: 0.9375rem;
  color: var(--darker-grayish-cyan);
}
.results__total-bill {
  grid-column: 2/3;
  grid-row: 1/3;
  text-align: end;
  font-size: 2.125rem;
  color: var(--strong-cyan);
}
.results__reset {
  width: 100%;
  height: 2.9375rem;
  color: var(--very-dark-cyan);
  background: var(--strong-cyan);
  border-radius: 0.5rem;
  margin-top: 1.5625rem;
  font-size: 1.5rem;
  font-weight: 600;
  text-transform: uppercase;
}
@media (min-width: 64em) {
  .results__reset {
    margin-top: 8.9375rem;
    font-size: 1.25rem;
  }
}
.results__reset:active {
  filter: brightness(1.5);
}